@keyframes hover {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translateY(0px) translateX(0vh)
	}

	25% {
		box-shadow: 5 10px 15px 5px rgba(0,0,0,0.2);
		transform: translateY(-.4vh) translateX()
	}

	50% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translateY(2px) translateX(0vh)
	}

    75% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translateY(.4vh) translateX(0vh)
	}

    100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translateY(0px) translateX(0vh)
	}
}
@keyframes gradient {
	
	0% { 
		background-image: linear-gradient(90deg, rgba(56,63,117,1) 0%, rgba(255,125,58,0) 55%, rgba(238,46,33,1) 100%);
		
	}
	50% { 
		background-image: linear-gradient(90deg, rgba(56,63,117,1) 0%, rgba(255,125,58,0.6685049019607843) 55%, rgba(238,46,33,1) 100%) ;
	}
	100% {
		background-image: linear-gradient(90deg, rgba(56,63,117,1) 0%, rgba(255,125,58,0) 55%, rgba(238,46,33,1) 100%) ;
	}


}